.emailForm {
    padding: 0 2rem;
    margin-bottom: 3rem;
    position: relative;

}

::placeholder {
    color: hsl(0, 36%, 70%);
    font-family: 'Josefin Sans', sans-serif;
}

input {
    width: 100%;
    height: 3rem;
    padding-left: 1.5rem;
    border-radius: 2rem;
    ;
    background-color: transparent;
    border: 1px solid hsla(0, 36%, 70%, 0.50);
    outline: none;
}

.outlineError {
    border: 1px solid hsl(0, 93%, 68%)
}

.hidden {
    display: none;
}

.iconError {
    display: visible;
    position: absolute;
    top: .75rem;
    right: 7.5rem;
}

button {
    position: absolute;
    top: 0;
    right: 2rem;
    height: 3rem;
    width: 4rem;
    border-radius: 2rem;
    background-image: linear-gradient(135deg, hsl(0, 80%, 86%), hsl(0, 74%, 74%));
    border: none;
    cursor: pointer;
}

button:hover {
    width: 5rem;
    background-image: #fff;
    opacity: 0.5;
}

.message {
    margin-top: .5rem;
    font-size: .75rem;
    text-align: left;
    padding-left: 1.5rem;
}

.success {
    color: green;
}

.error {
    color: hsl(0, 93%, 68%);
}


@media screen and (min-width: 768px) {
    .emailForm {
        grid-column: 1/2;
        width: 70%;
        justify-self: center;
        text-align: left;
        margin-bottom: 9rem;
        padding-right: 7rem;
    }

    input {
        height: 3.5rem;
    }

    ::placeholder {
        font-size: 1rem;
    }

    button {
        height: 3.5rem;
        right: 7rem;
        width: 6rem;
    }

    button:hover {
        width: 6rem;
    }

    .iconError {
        right: 14rem;
        top: 1rem;
    }


}